.header {
  background-color: #84a178;
  justify-content: space-between;
  padding-top: 20px;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
}

.header__logo {
  fill: #f8d24e;
  width: 18.6px;
  height: 37.1px;
  position: relative;
}

.header__logo-box {
  position: relative;
}

.header__logo-span {
  color: #f8d24e;
  font-size: 14.874px;
  position: absolute;
  bottom: 1px;
  right: -11px;
}

.header__menu {
  background-color: #0000;
  border: none;
}

.header__icon {
  width: 32px;
  height: 32px;
  fill: #fff;
}

.example {
  color: #424242;
  max-width: 960px;
  background-color: #fff;
  padding: 16px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.example code {
  color: #fff;
  background-color: #009688;
  border-radius: 2px;
  padding: 2px 6px;
  font-family: monospace;
  font-size: 16px;
  font-weight: 700;
}

.example .background {
  height: 240px;
  background-image: url("demo.e3ba131c.jpg");
  background-position: center;
  background-size: cover;
}

